import * as React from 'react'
import Table from 'src/packages/table/Table'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import Space from 'src/components/Space'
import { renderString } from 'src/utils/renderCode'
import Button from 'src/packages/button/Button'

const componentName = 'TableExample'

class TableExample extends React.Component<{}, any> {
  public static displayName = componentName

  public static propTypes = {}

  public static defaultProps = {}

  constructor(props: {}) {
    super(props)
    const nameArray = [
      'Jack',
      'Bob',
      'Ethan',
      'Jim',
      'Lucy',
      'Annie',
      'Lily',
      'Tom'
    ]
    const array = []
    for (let i = 0; i < 8; i++) {
      array.push({
        key: `id${i + 1}`,
        name: nameArray[i],
        age: Math.floor(Math.random() * 20 + 20 - 1),
        address: `${i + 1}`
      })
    }
    this.state = {
      columns: [
        {
          title: '',
          key: 'name',
          sort: true,
          width: 150
        },
        {
          title: '',
          key: 'age',
          sort: true,
          width: 100
        },
        {
          title: '',
          key: 'address'
        },
        {
          title: '',
          key: 'action',
          width: 150,
          extra: <Button type="danger"></Button>,
          extraHandlers: {
            onClick: (key: string, index: number) =>
              this.handleClickAction(key, index)
          }
        }
      ],
      dataSource: array
    }
  }

  public handleClickAction = (key: string, index: number) => {
    this.setState({
      dataSource: this.state.dataSource.filter((data: any) => data.key !== key)
    })
  }

  public render() {
    const cn = componentName

    return (
      <div className="table-example">
        <section>
          <h1>Table </h1>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
        </section>
        <div className="example">
          <div className="container" style={{ display: 'block' }}>
            <Table
              columns={this.state.columns}
              dataSource={this.state.dataSource}
              scroll={{ y: 300 }}
            />
          </div>
        </div>
        <div className="example">
          <div className="container" style={{ display: 'block' }}>
            <Table
              columns={this.state.columns}
              dataSource={this.state.dataSource}
              stripe={true}
            />
          </div>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th className="type"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>columns</td>
              <td className="des"> IColumn</td>
              <td className="type">IColumn[]</td>
              <td></td>
            </tr>
            <tr>
              <td>dataSource</td>
              <td className="des"> IData</td>
              <td className="type">IData[]</td>
              <td></td>
            </tr>
            <tr>
              <td>select</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td></td>
            </tr>
            <tr>
              <td>size</td>
              <td className="des"></td>
              <td className="type">'small' | 'medium' | 'large'</td>
              <td>'medium'</td>
            </tr>
            <tr>
              <td>loading</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
            <tr>
              <td>disabled</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
            <tr>
              <td>ghost</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
            <tr>
              <td>htmlType</td>
              <td className="des"> type </td>
              <td className="type">'button' | 'submit' | 'reset'</td>
              <td>'button'</td>
            </tr>
            <tr>
              <td>onClick</td>
              <td className="des"></td>
              <td className="type">React.MouseEventHandler</td>
              <td></td>
            </tr>
            <tr>
              <td>className</td>
              <td className="des"> Button </td>
              <td className="type">string</td>
              <td></td>
            </tr>
            <tr>
              <td>style</td>
              <td className="des"> Button </td>
              <td className="type">React.CSSProperties</td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default TableExample



